import React from "react";
import ReactDOM from 'react-dom';
import { SocialIcon } from 'react-social-icons';
ReactDOM.render(<SocialIcon url="https://twitter.com/jaketrent" />, document.body);

export default function Footer() {
    return (
      <section id="footer">
        <div className="container px-5 py-10 mx-auto">
          <div className="text-center mb-20">
            <h1 className="sm:text-4xl text-3xl font-medium title-font text-white mb-4">
              Follow me on Social Media
            </h1>
            <span class="fa fa-facebook" alt="facebook icon"></span>  
            <span class="fa fa-twitter" alt="twitter icon"></span>
            <span class="fa fa-linkedin" alt="linkedin profile"></span>  
            <span class="fa fa-instagram" alt="instagram profile"></span>  
          </div>
        </div>
       </section>
);
}    



